import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import BackgroundDiv from "./BackgroundDiv";
import colors from "../../constants/colors";

const styles = {
  contentDiv: {
    padding: "125px 102px",
    width: "90%",
    color: colors.black,
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: "Graphik"
  },
  textDiv: {
    padding: "30px",
    width: "30%",
    maxWidth: "450px",
    maxHeight: "400px",
    overflow: "scroll",

    position: "absolute",
    color: colors.black,
    fontSize: "18px",
    lineHeight: "1.5em",
    fontFamily: "Graphik",
    zIndex: "100",
    transition: "opacity 2s",
    opacity: "1"
  },
  textDivHidden: {
    padding: "30px",
    width: "28%",
    position: "absolute",
    color: colors.black,
    opacity: "0",
    fontSize: "18px",
    lineHeight: "32px",
    fontFamily: "Graphik",
    zIndex: "100",

  }
};

const ContentItem = ({ classes, text, state }) => (
  <div className={classes.contentDiv}>
    <div className={state===1 ? classes.textDiv : classes.textDivHidden}>{text}</div>
    <div className="trails-clip">
    <BackgroundDiv state={state}></BackgroundDiv>

    </div>

 </div>
);

ContentItem.propTypes = {
  classes: PropTypes.object,
  text: PropTypes.string
};

export default withStyles(styles)(ContentItem);
