import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { Image } from "semantic-ui-react";
//import artwork from "../../assets/img/artwork.png";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    //backgroundColor: "white",
    overflow: "hidden",
    height: "100vh",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center"
  }),
  logo: {
    height: "74%"
    //width: "90%"
  }
};

const AnimationSection = ({ classes, factor, offset, ribbon }) => {
  //console.log("ribbon "+ ribbon);
  return (
    <ParallaxLayer factor={factor} offset={offset} speed={0} >
      <div className={classes.componentDiv}>
        <Image src={ribbon} className={classes.logo} />
      </div>
    </ParallaxLayer>
  );
};

AnimationSection.propTypes = {
  classes: PropTypes.object,
  factor: PropTypes.number,
  offset: PropTypes.number,
  ribbon: PropTypes.string
};

export default withStyles(styles)(AnimationSection);
